:root {
  --black: #000000;
  --white: #ffffff;
  --item-background: linear-gradient(168.14deg, #2c354f -3.85%, #596d99 135.11%);
  --background: #e5e5e5;
  --yellow: #ffdd2d;
  --height: 512px;
  --start: #2c354f;
  --end: #596d99;
}
@font-face {
  font-family: "SF Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/sf-pro-regular.woff2") format("woff2"), url("../../fonts/sf-pro-regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/sf-pro-bold.woff2") format("woff2"), url("../../fonts/sf-pro-bold.woff") format("woff");
}
.body {
  position: relative;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "SF Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  background-color: var(--background);
  color: var(--white);
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.item {
  min-height: var(--height);
  box-sizing: border-box;
  padding-top: 50px;
  padding-right: 20px;
  padding-bottom: 73px;
  padding-left: 20px;
  background: linear-gradient(168.14deg, var(--start) -3.85%, var(--end) 135.11%);
}
.item__date {
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 10px;
  padding-left: 10px;
  font-size: 14px;
  line-height: 17px;
  border-radius: 24px;
  color: var(--black);
  background-color: var(--yellow);
}
